* {
  margin: 0;
  padding: 0;
}

.container-s3 {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.collage {
  display: flex;
  justify-content: center;
  position: relative;
  top: -91px;
  &__img {
    width: 100vw;
    padding: 0 93px;
  }
}
.card {
  display: flex;
  overflow-x: hidden;
  overscroll-behavior-x: contain;
  scroll-snap-type: x proximity;
  gap: 50px;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    min-width: 100%;
    scroll-snap-align: start;
    transition: transform 0.3s ease;
  }
  &__bg {
    background-color: #fafafa;
    padding: 39px 110px;
    width: 80%;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  &__sgv-qm {
    width: 54px;
    height: 54px;
    position: absolute;
    top: 13px;
  }
  &__p {
    font-size: 16px;
    line-height: 2;
    text-align: center;
    position: relative;
    font-family: DM Sans;
    font-weight: 500;
  }
  &__h2 {
    font-size: 20px;
    font-weight: 500;
    font-family: DM Sans;
  }
  &__sgv {
    width: 32px;
    height: 40px;
  }
  &__sgv2 {
    width: 72px;
    height: 72px;
  }
}

strong {
  box-shadow: 0px 2px #e1e1e1;
}

// media screen

@media screen and (min-width: 480px) and (max-width: 768px) {
  .collage {
    top: -254px;
    &__img {
      padding: 0px;
    }
  }
  .card {
    overflow-x: scroll;
    &__content {
      scroll-snap-align: start;
    }
  }
  .card__bg {
    padding: 39px 60px;
  }
  .card__p {
    font-size: 14px;
    font-weight: 500px;
  }
}
@media screen and (min-width: 0px) and (max-width: 479px) {
  .collage {
    top: -28px;
    &__img {
      padding: 0px;
    }
  }
  .card {
    overflow-x: scroll;
    &__content {
      scroll-snap-align: start;
    }
  }
  .card__bg {
    padding: 39px 60px;
  }
  .card__p {
    font-size: 12px;
    font-weight: 500px;
  }
}

//480 px, 768 px, 1280 px//
