.menu-container {
  visibility: hidden;
  width: 254px;
  height: 100vh;
  z-index: 999;
  stroke-width: 2px;
  stroke: #e18298;
  background-color: #e17992;
  padding: 20px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.menu-toggle {
  min-height: 12px;
  min-width: 20px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  margin-top: 46px;
  padding: 0;
  display: none;
  position: relative;
}

.x {
  position: absolute;
  bottom: 34px;
  right: 34px;
}

.menu-container .menu-toggle {
  position: left;
  color: #fff;
  top: 16px;
  right: 16px;
}

.menu-container.is-open {
  transform: translateX(0);
}

.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu .nav__link2 {
