:root {
  --primary-color: #d41443;
  --secondary-color: #ffa5ba;
  --tertiary-color: #000;
  --brand-color: #fff;
  --brand-color2: #fff5f6;
}

body {
  font-family: Titan One, DM Sans;
}

.description {
  background: var(--brand-color2);
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 15%;
}

.description__slogan {
  color: var(--secondary-color);
  text-align: center;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  letter-spacing: normal;
  padding-top: 60px;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.description__title {
  color: var(--primary-color);
  text-align: center;
  letter-spacing: 3.48px;
  text-transform: uppercase;
  letter-spacing: normal;
  margin-top: 10px;
  font-size: 58px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.detail {
  background-image: url("sectionbg1 1.5a4338ff.png");
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 90px;
  display: flex;
}

.detail__image {
  width: 80%;
  height: 80%;
  padding: 8px 0 20px 20px;
}

.detail__text {
  height: 100%;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  padding-left: 5%;
  display: flex;
}

.detail__text-1 {
  color: var(--tertiary-color);
  letter-spacing: .64px;
  width: 90%;
  height: 20%;
  margin-top: 50px;
  margin-bottom: 32px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.8;
}

.detail__text-2 {
  color: #907e82;
  letter-spacing: .56px;
  width: 90%;
  height: 20%;
  margin-bottom: 40px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.9;
}

